//   
import React from 'react';
import { ComponentPanel
	   , FlexPanel
	   , FullPanel
	   , SubFullPanel
	   , RelativeGroup
	   , TabPanel
	   , Tabs
	   , Grid             } from 'components';
import { ComLib
	   , DataLib
	   , StrLib
	   , TransManager
	   , newScrmObj            } from 'common';
import BOT010100 from '../BOT010100';
import BOT080200 from '../BOT010200';
import BOT080300 from '../BOT010300';

class View extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			dsSnroInitProcessList : DataLib.datalist.getInstance(),
			actionData : [],
			btnProps : {
				btnSearch : {
					id       : 'btnSearch',
					disabled : false,
					value    : '',
					hidden   : false
				},
				btnBigSave : {
					id       : 'btnBigSave',
					disabled : false,
					value    : '',
					hidden   : false
				},
	
				btnSmlSave : {
					id       : 'btnSmlSave',
					disabled : false,
					value    : '',
					hidden   : false
				},
			},	
			
			grdProps : {		
				grdSnroInitProcess : {
					id : 'grdSnroInitProcess',
					areaName : ' ',
					header: [						
								{headerName: '',field: 'SNRO_CD',	       colId: 'SNRO_CD',		editable: false, width: 80 },
								{headerName: '',field: 'PROCESS_TP_NM',    colId: 'PROCESS_TP_NM',	editable: false, width: 100},
								{headerName: ' ',	field: 'SNRO_EXPL',	colId: 'SNRO_EXPL',	    editable: false, width: 300 },
								{headerName: '', field: 'PROCESS'   ,	colId: 'PROCESS'   ,	editable: true, width: 90,
									cellRenderer: 'actionButton', 
									fiiled: true,
									color: 'blue'},
							],
					height: '670px'
				},
			},
			selectboxProps : {
				selUseYn : {
					id       : 'selUseYn',
					value    : '',
					dataset  : ComLib.convComboList(ComLib.getCommCodeList("USE_FLAG"), newScrmObj.constants.select.argument.all),
					width    : 80,
					selected : 0,
					disabled : false
				}
			},			
			textFieldProps : {
				iptBigCdNm : {
					id          : 'iptBigCdNm',
					name        : 'iptBigCdNm',
					value       : '',
					placeholder : '/',
					minLength   : 1,
					maxLength   : 12,
					readOnly    : false,
					disabled    : false
				}
			},
		}

		this.event.button.onClick   = this.event.button.onClick.bind(this);
		this.event.input.onChange   = this.event.input.onChange.bind(this);
		
	}
	/*------------------------------------------------------------------------------------------------*
		0) Custom Event Zone 
		rowFinder =           
	 ------------------------------------------------------------------------------------------------*/
	rowFinder = (targetRecords, targetColumn, currentRow, secondColumn, secondRow) => {
		let rowNum = 0;

		if (targetRecords === undefined || targetRecords === null) {
			return rowNum;
		}

		if (StrLib.isNull(secondColumn)) {
			if (!StrLib.isNull(currentRow)) {
				for (let i = 0; i < targetRecords.length; i++) {
					if (targetRecords[i][targetColumn] === currentRow) {
						rowNum = i;

						break;
					}
				}
			}
			return rowNum;

		} else {
			if (!StrLib.isNull(secondRow)) {
				for (let i = 0; i < targetRecords.length; i++) {
					if (targetRecords[i][targetColumn] === currentRow && targetRecords[i][secondColumn] === secondRow) {
						rowNum = i;

						break;
					}
				}
			}
			return rowNum;
		}
	}
	/*------------------------------------------------------------------------------------------------*
		1) componentDidMount () => init    
		=>   ,  ->    this.setState  ,    render()   ->   
	 ------------------------------------------------------------------------------------------------*/
	componentDidMount () {
		this.transaction("SYS080000_R00")
	}
	/*------------------------------------------------------------------------------------------------*
		2) componentDidUpdate () =>     (    )
		=> prevProps  props    ->    setState  ,    ->  setState   
	 ------------------------------------------------------------------------------------------------*/
	componentDidUpdate (prevProps, prevState, snapshot) {
		// console.log("updated!!");
		// console.log(this.state.dsGrp);
	}
	/*------------------------------------------------------------------------------------------------*
		3) componentWillUnmount () =>      
		=>  ,      ->    setState  
	 ------------------------------------------------------------------------------------------------*/
	 componentWillUnmount () {

	}

	/*------------------------------------------------------------------------------------------------*/
	// [3. validation Event Zone]
	//  - validation  
	// SYS010000_R01 :   
	// SYS010000_R02 :   
	// SYS010000_H01 :   
	// SYS010000_H02 :   
	/*------------------------------------------------------------------------------------------------*/
	validation = (...params) => {
		let transId = params[0];
		let chkCnt  = 0;
		let returnVal = -1;

		switch (transId) {
		case 'SYS010000_R01':

			break;
		default: break;
		}

		return true;
	}
	/*------------------------------------------------------------------------------------------------*/
	// [4. transaction Event Zone]
	//  - transaction  
	// SYS010000_R01 :   
	// SYS010000_R02 :   
	// SYS010000_H01 :   
	// SYS010000_H02 :   
	/*------------------------------------------------------------------------------------------------*/
	transaction = (...params) => {
		let transId = params[0];

		let transManager = new TransManager();

		transManager.setTransId (transId);
		transManager.setTransUrl(transManager.constants.url.common);
		transManager.setCallBack(this.callback);

		try  {
			switch (transId) {
			case 'SYS080000_R00':
				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "SYS.R_getSnroInitProcess",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroInitProcessListRecv"
				});

				transManager.addDataset('dsEmpty', [{}]);
				transManager.agent();

				break;			
			case 'SYS080000_R01':
				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "SYS.R_getSnroList",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroListRecv"
				});

				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "SYS.R_getSnroTtsList",
					datasetsend: "dsEmpty",
					datasetrecv: "dsSnroTtsListRecv"
				});
				
				transManager.addConfig  ({
					dao        : transManager.constants.dao.base,
					crudh      : transManager.constants.crudh.read,
					sqlmapid   : "SYS.R_getInterList",
					datasetsend: "dsEmpty",
					datasetrecv: "dsInterListRecv"
				});

				transManager.addDataset('dsEmpty', [{}]);
				transManager.agent();

				break
			default: break;
			}
		} catch (err) {

		}
	}
	/*------------------------------------------------------------------------------------------------*/
	// [5. Callback Event Zone]
	//  - Callback  
	// SYS010000_R01 :   
	// SYS010000_R02 :   
	// SYS010000_H01 :   
	// SYS010000_H02 :   
	/*------------------------------------------------------------------------------------------------*/
	callback = (res) => {	

		switch (res.id) {
		case 'SYS080000_R00':
			if (res.data.dsSnroInitProcessListRecv.length > 0) {
				let dsSnroInitProcessListRecv = res.data.dsSnroInitProcessListRecv;

				ComLib.setStateInitRecords(this, "dsSnroInitProcessList", dsSnroInitProcessListRecv);

			} else {
				ComLib.setStateInitRecords(this, "dsSnroInitProcessList", []);

			}
			break;
			
		case 'SYS080000_R01':	
			let params = this.state.actionData;
			let option1 = { width: '1200px', height: '830px', modaless: false, params, dsSnroTts: res.data.dsSnroTtsListRecv, dsSnro:res.data.dsSnroListRecv, dsInter: res.data.dsInterListRecv}
			ComLib.openPop('SYS080001', '  ', option1);

			break;

		default : break;
		}
	}
	/*------------------------------------------------------------------------------------------------*/
	// [6. event Zone]
	//  -  Component event 
	/*------------------------------------------------------------------------------------------------*/
	event = {
		//  
		button : {
			onClick : (e) => {
				switch (e.target.id) {
				case "btnSearch":
					if (this.validation("SYS010000_R01")) this.transaction("SYS010000_R01");
				
					break;

				case "btnBigSave":
					if (this.validation("SYS010000_H01")) this.transaction("SYS010000_H01");

					break;

				case "btnSmlSave":
					if (this.validation("SYS010000_H02")) this.transaction("SYS010000_H02");
					
					break;	
						
				default : break;
				}
			}
		},
		grid: {
			onGridReady: (e) => {
				switch (e.id) {
				case "grdSnroInitProcess":
					this.snroInitProcessGridApi = e.gridApi;
					this.snroInitProcessGrid    = e.grid;
					
					break;

				default: break
				}
			},
			onActionCellClicked : (e) => {				
				switch (e.id) {
				case "grdSnroInitProcess":	
					switch (e.col) {
					case "PROCESS":
						this.setState({...this.state, actionData: e.data}, this.transaction('SYS080000_R01'))
						break;

					default: break;
					}

					break;

				default: break;
				}
			}
		},
		input : {
			onChange: (e) => {
				switch (e.target.id) {
				case 'iptBigCdNm':
					let state = this.state;

					state['textFieldProps']['iptBigCdNm'].value = e.target.value;
	
					this.setState(state);
					
					break;

				default: break;
				}
			},
			onKeyPress: (e) => {
				switch (e.target.id) {
				case 'iptBigCdNm':
					if (e.key === 'Enter') {
						if (this.validation("SYS010000_R01")) this.transaction("SYS010000_R01");
					}
					
					break;

				default: break;
				}

			}
		},
		selectbox: {
			onChange: (e) => {
				let state = this.state;

				state['selectboxProps'][e.target.id].selected = e.target.selectedIndex;
				state['selectboxProps'][e.target.id].value    = e.target.value;

				this.setState(state);

			}
		},
		tab : {
			onClick : (e) => {
				console.log('onClick');
			}
		},
	}
	/*------------------------------------------------------------------------------------------------*/
	// [7. render Zone]
	//  -    
	/*------------------------------------------------------------------------------------------------*/
	render () {
		return (
			<React.Fragment>
				<FullPanel>		
				<SubFullPanel>	
					<FlexPanel>
						<ComponentPanel>
							<RelativeGroup>									
								<Tabs tabWidth='100px' onClick = {this.event.tab.onClick}>
									<TabPanel id = {'BOT010100'} index={0} label={' '}>
										{/* <BOT010100 cmpWordList={this.state.cmpWordList}/> */}
										
										<BOT010100/>
									</TabPanel>
									<TabPanel id = {'BOT080200'} index={1} label={'TTS '}>
										<BOT080200/>
									</TabPanel>
									<TabPanel id = {'BOT080300'} index={2} label={' '}>
										<BOT080300/>
									</TabPanel>									
								</Tabs>		
							</RelativeGroup>
						</ComponentPanel>		
						<ComponentPanel>
							<Grid
								id        = {this.state.grdProps.grdSnroInitProcess.id} 
								areaName  = {this.state.grdProps.grdSnroInitProcess.areaName}
								header    = {this.state.grdProps.grdSnroInitProcess.header}
								data      = {this.state.dsSnroInitProcessList}
								height    = {this.state.grdProps.grdSnroInitProcess.height}
								rowNum    = {true}
								addRowBtn = {false}
								delRowBtn = {false}

								onGridReady        = {this.event.grid.onGridReady}
								onActionCellClicked= {this.event.grid.onActionCellClicked}
												
							/>
						</ComponentPanel>
					</FlexPanel>
					</SubFullPanel>	
				</FullPanel>
			</React.Fragment>
		)
	}
}
export default View;